.box {
    background: none;
    border: none;
    width: max-content;
    /* margin: 0.5vh 2vw; */
    padding: 1vh 1vw;
    border-radius: 3px;
    background-color: var(--secondary-color);
    box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.2), 0 3px 10px 0 rgba(0, 0, 0, 0.2);
    font-size: 1.2rem;
}